import * as React from "react";
import * as Styles from "components/palette/ShapesPalette.css";
import {connect} from "react-redux";
import PaletteItem from "components/palette/PaletteItem";
import {dropShapeToCanvas} from "actions";
import {shapeFactory} from "components/shapes/Shapes";
import {selectShapeOnPalette} from "actions";
import {IAppState, ShapeType} from "state";

interface IProps {
  selectedShapeType?: ShapeType;
}

interface IReduxActions {
  selectShapeOnPalette: typeof selectShapeOnPalette;
  dropShapeToCanvas: typeof dropShapeToCanvas;
}

function mapStateToProps(state: IAppState): IProps {
  return {
    selectedShapeType: state.palette.selectedShapeType,
  };
}

class ShapesPalette extends React.Component<IProps & IReduxActions> {
  private readonly PaletteItemPadding = 20;
  public render(): JSX.Element {
    return (
      <div className={Styles.palette}>
        {this.renderPaletteItem(ShapeType.Circle)}
        {this.renderPaletteItem(ShapeType.Square)}
        {this.renderPaletteItem(ShapeType.Triangle)}
      </div>
    );
  }

  private renderPaletteItem(shapeType: ShapeType): JSX.Element {
    const {selectedShapeType} = this.props;
    return (
      <PaletteItem shapeType={shapeType} onClick={this.clickHandler} onDrop={this.dropHandler}>
        {shapeFactory(shapeType, {
          x: this.PaletteItemPadding,
          y: this.PaletteItemPadding,
          isSelected: selectedShapeType === shapeType,
        })}
      </PaletteItem>
    );
  }

  private clickHandler = (shapeType: ShapeType) => {
    this.props.selectShapeOnPalette(shapeType);
  };

  private dropHandler = (shapeType: ShapeType, x: number, y: number) => {
    this.props.dropShapeToCanvas(shapeType, x + this.PaletteItemPadding, y + this.PaletteItemPadding);
  };
}

export default connect(
  mapStateToProps,
  {selectShapeOnPalette, dropShapeToCanvas}
)(ShapesPalette);
