import {ShapeType} from "state";

export const SELECT_SHAPE = "SELECT_SHAPE";
export const DROP_SHAPE = "DROP_SHAPE";
export const SELECT_SHAPE_ON_CANVAS = "SELECT_SHAPE_ON_CANVAS";
export const MOVE_SHAPE_ON_CANVAS = "MOVE_SHAPE_ON_CANVAS";

export interface IAction {
  type: string;
}

export interface IPaletteAction extends IAction {
  shapeType: ShapeType;
}

export interface IShapeDropAction extends IPaletteAction {
  x: number;
  y: number;
}

export interface ISelectShapeOnCanvasAction extends IAction {
  index: number | null;
}

export interface IMoveShapeOnCanvasAction extends IAction {
  dx: number;
  dy: number;
}

export type TAction = IPaletteAction | IShapeDropAction | ISelectShapeOnCanvasAction | IMoveShapeOnCanvasAction;

export const selectShapeOnPalette = (shapeType: ShapeType): IPaletteAction => {
  return {type: SELECT_SHAPE, shapeType};
};

export const dropShapeToCanvas = (shapeType: ShapeType, x: number, y: number): IShapeDropAction => {
  return {type: DROP_SHAPE, shapeType, x, y};
};

export const selectShapeOnCanvas = (index: number | null): ISelectShapeOnCanvasAction => {
  return {type: SELECT_SHAPE_ON_CANVAS, index};
};

export const moveShapeOnCanvas = (dx: number, dy: number): IMoveShapeOnCanvasAction => {
  return {type: MOVE_SHAPE_ON_CANVAS, dx, dy};
};
