import * as React from "react";
import * as Styles from "components/palette/ShapesPalette.css";
import {ShapeType} from "state";
import {ConnectDragSource, DragSource, DragSourceMonitor, DragSourceCollector, DragSourceSpec} from "react-dnd";

interface IDropResult {
  x: number;
  y: number;
}

interface IPaletteItemProps {
  shapeType: ShapeType;
  connectDragSource?: ConnectDragSource;
  onClick(shapeType: ShapeType): void;
  onDrop(shapeType: ShapeType, x: number, y: number): void;
}

const shapeSource: DragSourceSpec<IPaletteItemProps> = {
  beginDrag: (props: IPaletteItemProps) => {
    // Return the data describing the dragged item
    return {shapeType: props.shapeType};
  },

  endDrag(props: IPaletteItemProps, monitor: DragSourceMonitor): void {
    if (!monitor.didDrop()) {
      return;
    }

    // When dropped on a compatible target, do something
    const dropResult = monitor.getDropResult() as IDropResult;
    const {onDrop, shapeType} = props;
    onDrop(shapeType, dropResult.x, dropResult.y);
  },
};

const collect: DragSourceCollector = (connector, monitor) => {
  return {
    connectDragSource: connector.dragSource(),
    isDragging: monitor.isDragging(),
  };
};

class PaletteItem extends React.Component<IPaletteItemProps> {
  public render(): JSX.Element {
    const {connectDragSource} = this.props;
    return (connectDragSource as ConnectDragSource)(
      <button onClick={this.clickHandler} className={Styles.paletteItem}>
        <svg width={100} height={100}>
          {this.props.children}
        </svg>
      </button>
    );
  }

  private clickHandler = () => {
    this.props.onClick(this.props.shapeType);
  };
}

export default DragSource("SHAPE", shapeSource, collect)(PaletteItem);
