import * as React from "react";
import * as ReactDOM from "react-dom";
import {
  DropTarget,
  DropTargetConnector,
  DropTargetMonitor,
  ConnectDropTarget,
  DropTargetSpec,
  ClientOffset,
} from "react-dnd";
import {connect} from "react-redux";
import * as Styles from "components/Canvas.css";
import {selectShapeOnCanvas, moveShapeOnCanvas} from "actions";
import {IAppState, IShape} from "state";
import {shapeFactory} from "components/shapes/Shapes";

const dropTarget: DropTargetSpec<ICanvasProps & ICanvasDropProps & IReduxActions> = {
  drop(props: ICanvasProps, monitor?: DropTargetMonitor, component?: React.Component<ICanvasDropProps>): object {
    const node = ReactDOM.findDOMNode(component as React.ReactInstance);
    const rect = (node as Element).getBoundingClientRect() as DOMRect;

    const clientOffset = (monitor && monitor.getSourceClientOffset()) as ClientOffset;

    return {name: "Canvas", x: clientOffset.x - rect.x, y: clientOffset.y - rect.y};
  },
};

function mapStateToProps(state: IAppState): ICanvasProps {
  return {shapes: state.canvas.shapes, selectedShapeIndex: state.canvas.selectedShapeIndex};
}

const collect = (connector: DropTargetConnector) => ({
  connectDropTarget: connector.dropTarget(),
});

interface ICanvasProps {
  shapes: IShape[];
  selectedShapeIndex: number | null;
}

interface ICanvasState {
  movementStartPoint: IPoint | null;
  selectedFigureOffset: IOffset;
}

interface ICanvasDropProps {
  connectDropTarget?: ConnectDropTarget;
}

interface IReduxActions {
  moveShapeOnCanvas: typeof moveShapeOnCanvas;
  selectShapeOnCanvas: typeof selectShapeOnCanvas;
}

interface IPoint {
  x: number;
  y: number;
}

interface IOffset {
  dx: number;
  dy: number;
}

class CanvasBase extends React.Component<ICanvasProps & ICanvasDropProps & IReduxActions, ICanvasState> {
  constructor(props: ICanvasProps & ICanvasDropProps & IReduxActions) {
    super(props);

    this.state = {
      movementStartPoint: null,
      selectedFigureOffset: {dx: 0, dy: 0},
    };
  }

  public render(): JSX.Element {
    const {connectDropTarget, shapes} = this.props;

    return (connectDropTarget as ConnectDropTarget)(
      <svg className={Styles.canvas}>{shapes.map(this.renderShape)}</svg>
    );
  }

  private renderShape = (shape: IShape, index: number): JSX.Element => {
    const {selectedShapeIndex} = this.props;
    let {x, y} = shape;
    const isSelected = index === selectedShapeIndex;
    if (isSelected) {
      x += this.state.selectedFigureOffset.dx;
      y += this.state.selectedFigureOffset.dy;
    }

    return shapeFactory(shape.shapeType, {
      x,
      y,
      isSelected,
      onMouseDown: (e: React.MouseEvent<SVGElement>) => {
        this.mouseDownHandler(index, e.clientX, e.clientY);
      },
    });
  };

  private mouseMoveHandler = (e: MouseEvent) => {
    if (this.state.movementStartPoint) {
      const {x, y} = this.state.movementStartPoint;
      this.setState({
        selectedFigureOffset: {
          dx: e.clientX - x,
          dy: e.clientY - y,
        },
      });
    }
  };

  private mouseUpHandler = () => {
    document.removeEventListener("mousemove", this.mouseMoveHandler);
    document.removeEventListener("mouseup", this.mouseUpHandler);

    const {dx, dy} = this.state.selectedFigureOffset;
    this.props.moveShapeOnCanvas(dx, dy);

    this.setState({
      selectedFigureOffset: {
        dx: 0,
        dy: 0,
      },
    });
  };

  private mouseDownHandler = (index: number, x: number, y: number) => {
    this.props.selectShapeOnCanvas(index);
    this.setState({movementStartPoint: {x, y}});
    document.addEventListener("mouseup", this.mouseUpHandler);
    document.addEventListener("mousemove", this.mouseMoveHandler);
  };
}

export const Canvas = connect(
  mapStateToProps,
  {selectShapeOnCanvas, moveShapeOnCanvas}
)(DropTarget("SHAPE", dropTarget, collect)(CanvasBase));
