import * as React from "react";
import * as Styles from "components/shapes/Shapes.css";
import {IProps, ShapeSize} from "components/shapes/Shapes";

export class Triangle extends React.Component<IProps> {
  public render(): JSX.Element {
    const h = ShapeSize * 0.86;
    const {x, y, isSelected, onMouseDown} = this.props;
    const classNames = [Styles.shape];
    if (isSelected) {
      classNames.push(Styles.selected);
    }
    const points: string[] = [[x + ShapeSize / 2, y].join(","), [x, y + h].join(","), [x + ShapeSize, y + h].join(",")];
    return <polygon points={points.join(" ")} className={classNames.join(" ")} onMouseDown={onMouseDown} />;
  }
}

export default Triangle;
