import * as React from "react";
import * as Styles from "components/shapes/Shapes.css";
import {IProps, ShapeSize} from "components/shapes/Shapes";

export class Circle extends React.Component<IProps> {
  public render(): JSX.Element {
    const {x, y, isSelected, onMouseDown} = this.props;
    const classNames = [Styles.shape];
    if (isSelected) {
      classNames.push(Styles.selected);
    }
    return (
      <circle
        cx={x + ShapeSize / 2}
        cy={y + ShapeSize / 2}
        r={ShapeSize / 2}
        className={classNames.join(" ")}
        onMouseDown={onMouseDown}
      />
    );
  }
}

export default Circle;
